//import React, {useCallback, useEffect, useState} from 'react';
import React, {useEffect, useState} from 'react';
import {useSelector} from 'react-redux';
import moment from 'moment';
//import {Button, Grid} from '@mui/material';
import {Button} from '@mui/material';
import DataTable from 'components/@extended/DataTable';
import {useNavigate} from 'react-router-dom';
import {getCampBookingList} from 'api/booking';
import {getCampsiteList} from 'api/camp';

const BookingList = () => {
	const navigate = useNavigate();

	const [data, setData] = useState([]);
	const [isLoading, setLoading] = useState(false);
	const user = useSelector((state) => state.user);
	const {memberId} = user;

	// render  
	const render = (rowData) => {
		const date = moment(rowData.createdAt);
		const formattedDate = date.format('YY.MM.DD');

		// rowData.startDate  Date  
		const startDate = new Date(rowData.startDate);

		// Date    
		const formattedStartDate = startDate.toLocaleDateString('ko-KR', {
			year: '2-digit', //  2 
			month: '2-digit', //  2 
			day: '2-digit', //  2 
			weekday: 'short', //   
		});

		// rowData.startDate  Date  
		const endDate = new Date(rowData.endDate);

		// Date    
		const formattedEndDate = endDate.toLocaleDateString('ko-KR', {
			year: '2-digit', //  2 
			month: '2-digit', //  2 
			day: '2-digit', //  2 
			weekday: 'short', //   
		});

		// Date    
		const endTime = endDate.getTime();
		const startTime = startDate.getTime();

		//     
		const diffTime = endTime - startTime; //    
		const diffDay = diffTime / (1000 * 60 * 60 * 24); //    

		const currentDate = new Date(); //  
		const diff = currentDate.getTime() - endTime; //   
		const days = Math.floor(diff / (1000 * 60 * 60 * 24)); //   

		//      
		const handleClick = () => {
			// /review/{rowData.id} 
			navigate(`/createCampReview/${rowData.bookingId}`);
		};
		return (
			<>
				<div
					style={{
						display: 'inline-block', //     
						border: '1px solid gray', //   
						borderRadius: '10px', //   
						padding: '5px', //     
						boxShadow: '3px 3px 5px rgba(0,0,0,0.3)', //   
						width: '400px', //  100 
					}}
				>
					<div
						style={{
							display: 'flex', // flexbox  
							alignItems: 'center', //  
							justifyContent: 'left', //  
							fontSize: '20px', //   20 
						}}
					>
						<b>{<div>{formattedDate}</div>}</b>
					</div>
					<br></br>
					<div
						style={{
							display: 'flex', // flexbox  
							alignItems: 'center', //  
							justifyContent: 'left', //  
						}}
					>
						<b>
							{/* switch   */}
							{
								<div
									style={{
										display: 'inline-block', //     
										border: '1px solid gray', //   
										color: 'gray', //   
										borderRadius: '10px', //   
										padding: '5px', //     
										margin: '7px', // div  10  
									}}
								>
									{(() => {
										switch (rowData.status) {
											case 'PAYMENT':
												return '';
											case 'BOOKING':
												return '';
											case 'DONE':
												return '';
											case 'CANCLED':
												return '';
											case 'USING':
												return '';
											case 'COMPLETE':
												return '';
											case 'UNCOMPLETED':
												return '';
											default:
												return '';
										}
									})()}
								</div>
							}
						</b>
					</div>
					<div
						style={{
							display: 'flex', // flexbox  
							alignItems: 'center', //  
							justifyContent: 'left', //  
							margin: '5px', // div  10  
						}}
					>
						<img
							src={
								require('../../assets/images/user_image.png')
									.default
							}
							alt='campsite'
							height='90'
							width='90'
							style={{objectFit: 'cover', borderRadius: '10px'}}
						/>
						<div
							style={{
								display: 'block', //   
								marginLeft: '10px',
								justifyContent: 'left',
							}}
						>
							{' '}
							<b>{rowData.memberName}</b>
							<br></br>
							{rowData.siteName}
							<br></br>
							<br></br>
							 {rowData.people ? rowData.people : 1}
						</div>
					</div>

					<div
						style={{
							display: 'flex', // flexbox  
							alignItems: 'center', //  
							justifyContent: 'left', //  
						}}
					>
						<div
							style={{
								backgroundColor: 'lightgray', //    
								borderRadius: '5px', //   
								margin: '5px', // div  10  
								justifyContent: 'left',
								width: '120px', //  100 
							}}
						>
							{' '}
							
							<br></br>
							<b>{formattedStartDate}</b>
						</div>
						<div
							style={{
								backgroundColor: 'lightgray', //    
								borderRadius: '5px', //   
								mamargin: '5px', // div  10  
								justifyContent: 'left',
								width: '120px', //  100 
							}}
						>
							{' '}
							
							<br></br>
							<b>{formattedEndDate}</b>
						</div>
						<div
							style={{
								backgroundColor: 'lightgray', //    
								borderRadius: '5px', //   
								margin: '5px', // div  10  
								justifyContent: 'left',
								width: '60px', //  100 
							}}
						>
							{' '}
							<br></br>
							<b>{diffDay}</b>
						</div>
						<div
							style={{
								backgroundColor: 'lightgray', //    
								borderRadius: '5px', //   
								margin: '5px', // div  10  
								justifyContent: 'left',
								width: '60px', //  100 
							}}
						>
							{' '}
							<br></br>
							<b>{rowData.people ? rowData.people : 1}</b>
						</div>
					</div>
					{days >= 0 && days <= 10 && (
						<Button
							disableElevation
							//		disabled={isSubmitting}
							fullWidth
							size='large'
							type='submit'
							variant='contained'
							color='primary'
							margin='7px' // div  10  
							onClick={handleClick} // onClick   
						>
							 
						</Button>
					)}
					{((rowData.status == 'DONE' && days > 10) ||
						rowData.status == 'UNCOMPLETED') && (
						<Button
							disableElevation
							//		disabled={isSubmitting}
							fullWidth
							size='large'
							type='submit'
							variant='contained'
							color='primary'
							margin='7px' // div  10  
						>
							   
						</Button>
					)}
					<div>{rowData.address}</div>
				</div>
			</>
		);
	};

	const columns = [
		{
			id: 'booking',
			width: 600,
			align: 'center',
			render: render, // render  
		},
	];

	useEffect(() => {
		findBookingList();
	}, [memberId]);

	const findBookingList = async () => {
		setLoading(true);

		console.log('   : ' + memberId);
		const response = await getCampBookingList({memberId});
		//setData(response);
		const campsiteResult = await getCampsiteList();
		const campsiteMap = {};
		for (let campsite of campsiteResult) {
			campsiteMap[campsite.id] = campsite.campsiteThumImage;
		}
		const data = response.map((item) => ({
			...item,
			campsiteThumImage: campsiteMap[item.siteId],
		}));

		setData(data);
		setLoading(false);
	};

	// const moveRegistCampsite = () => {
	// 	navigate(`/campsite/regist`);
	// };

	// const rowClick = useCallback((e, row) => {
	// 	const campsiteId = row.id;
	// 	navigate(`/campsite/${campsiteId}`);
	// }, []);

	return (
		<>
			{/* <Grid
				container
				direction='row'
				justifyContent='flex-end'
				spacing={2}
			>
				<Grid item>
					<Button variant='contained' onClick={moveRegistCampsite}>
						
					</Button>
				</Grid>
			</Grid> */}
			<DataTable
				columns={columns}
				rows={data}
				rowsPerPageOptions={[10, 20, 30]}
				isLoading={isLoading}
				//		rowClick={rowClick}
			/>
		</>
	);
};
export default BookingList;
